import React from "react";
import PropTypes from "prop-types";
import withStyles from "../../withstyle";
import { NavbarRightStyle } from "./NavbarRight.style";

import Image from "../../atoms/Image/image";
import Button from "../../atoms/Button/Button";
import Icon from "../../atoms/Icon/icon";
import {useHistory} from 'react-router-dom';
//* Created the NavbarRightItems which includes a button, button icon and a user profile image
const NavbarRightItems = ({
  onClickEventAudit,
  onClickEventBell,
  text,
  type,
  size,
  src,
  alt,
  varient,
  padder,
  className,
}) => {
  const history = useHistory();
  const handleClick = () =>{
    history.push('/audit')
  }
  return (
    <div className={`${className}`}>
      <div className={padder}>
        <Button
          onClick={onClickEventAudit}
          style={{
            color: "white",
            border: "1px solid white",
            borderRadius: "30px",
            padding: "10px 15px",
            backgroundColor: "transparent",
            boxShadow:
              "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
          }}
          btnText={text}
          aria-label={text}
          onClick = {handleClick}
          className="startauditbutton"
        />
      </div>
      <div className={padder}>
        <Icon
          iconClassName={type}
          size={size}
          onClick={onClickEventBell}
          className="icon"
        ></Icon>
      </div>
      <div className={padder}>
        <Image
          src={src}
          alt={alt}
          varient={varient}
          aria-label="Profile Image"
          className="userimage"
        />
      </div>
    </div>
  );
};

NavbarRightItems.propTypes = {
  onClickEventAudit: PropTypes.func,
  onClickEventBell: PropTypes.func,
  sizeButton: PropTypes.string,
  text: PropTypes.string,
  type: PropTypes.string,
  size: PropTypes.string,
  src: PropTypes.string,
  alt: PropTypes.string,
  padder: PropTypes.string,
  varient: PropTypes.string,
};

//* Default props sent
NavbarRightItems.defaultProps = {
  onClickEventAudit: () => console.log("Test Audit"),
  onClickEventBell: () => console.log("Test Bell"),
  sizeButton: "small",
  text: "START AUDIT",
  type: "faBell",
  size: "sm",
  src:
    "data:image/png;base64,",
  alt: "Altnernate text for logo.png",
  padder: "padder",
  varient: "small-radius",
};

export default withStyles(NavbarRightItems, NavbarRightStyle);

export { NavbarRightItems as NavbarRightItemsNotStyled };
