import React from "react";
import PropTypes from "prop-types";
import withStyles from "../../withstyle"
import { ImageStyle } from "./image.style";

const Image = ({ src, alt, varient, className, height, style, ...otherProps}) => {
  return (
      <img src={src} alt={alt} style={{...style}} varient={varient} height={height} {...otherProps} className={`${className}`} data-testid="image1" />
  );
};

Image.propTypes = {
  src: PropTypes.string,
  alt: PropTypes.string,
  varient: PropTypes.string,
  className: PropTypes.string,
};

Image.defaultProps = {
  src:
    "data:image/png;base64,",
  alt: "",
  varient: "",
  height:""
};

export default withStyles(Image, ImageStyle);

export { Image as ImageNotStyled }
