import React from "react";

import { CarouselMainStyle } from "./CarouselMain.style";
import withStyles from "../../withstyle";
import Carousel from "../../molecules/Carousel/Carousel";
import NavFooter from "../../molecules/navFooter/navFooter";

const CarouselMain = ({ className }) => {
  var NavFooterData = [
    { text: "About us", href: "about", varient: "secondary" },
    { text: "Help Centre", href: "help", varient: "secondary" },
    { text: "Contacts", href: "contacts", varient: "secondary" },
    { text: "Terms", href: "terms", varient: "secondary" },
    { text: "Privacy Policy", href: "policy", varient: "secondary" },
    { text: "Cookies", href: "cookies", varient: "secondary" },
  ];

  const args = {
    slides: [
      {
        src:
          "data:image/jpeg;base64,",
        alt: "tree",
        heading: "Don't let a messy audit be your fate",
      },
      {
        src:
          "data:image/jpeg;base64,",
        alt: "temp text",
        heading: "Don't let a messy audit be your fate",
      },
      {
        src:
          "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRLMl4_ScwHun3sWMAmlDsshDOzMCpJIWXsXQ&usqp=CAU",
        alt: " temp text",
        heading: "Don't let a messy audit be your fate",
      },
    ],
    interval: 2000,
    showArrows: false,
    width: "100%",
    height: "50%",
    size: "auto",
    showThumbs: false,
    autoPlay: true,
    showStatus: false,
    infiniteLoop: true,
  };

  return (
    <div className={`${className}`}>
      <div className="carouselPosition">
        <Carousel {...args} />
      </div>
      <div className="navFooterPosition">
        <NavFooter props={NavFooterData} />
      </div>
    </div>
  );
};

export default withStyles(CarouselMain, CarouselMainStyle);

export { CarouselMain as CarouselMainNotStyled };
