import React from 'react';
import withStyles from "../../withstyle";
import Navbarleft from '../../molecules/NavbarLeft/NavbarLeft';
import Navbarright from '../../molecules/NavbarRight/NavbarRight'
import Searchbar from '../../molecules/SearchBar/SearchBar'
import {TopnavStyle} from './Topnav.style'
import JSONDATA from "../../molecules/SearchBar/result.json";


const Topnav = ({btnText, onClickeventArrow, className}) =>{
  console.log(typeof onClickeventArrow);
  console.log(btnText);
    var searchProps = {
        input: {
          name: "username",
          type: "search",
          placeholder: "Search By Audit name or Project name",
          ariaLabel: "Search",
        },
        anchor: {
          className: "anchor",
          varient: "primary",
        },
      };
    var  NavbarLeftItemsProps = {
        content: "logo",
        onClickeventArrow: () => {return onClickeventArrow()},
        // theme: {
        //   color: "white",
        //   borderRadius: "radius30",
        // },
        sizeButton: "small",
        btnText: {btnText},
        size: "2x",
        varient: ""
      };
    var NavbarRightItemsProps = {
        onClickEventAudit: () => console.log("Test Audit"),
        onClickEventBell: () => console.log("Test Bell"),
        sizeButton: "small",
        text: "START AUDIT",
        type: "far faBell",
        size: "lg",
        src:
          "data:image/png;base64,",
        alt: "Altnernate text for logo.png",
        padder:"padder",
        varient: "small-radius"
      };
      console.log({...NavbarLeftItemsProps});
    return (
        <div className={`${className}`}>  
          <Navbarleft {...NavbarLeftItemsProps} className="navbarleft"/>
          <div>
            <Searchbar searchProps={searchProps} JSONDATA={JSONDATA.results} className="Searchbar" />
          </div>
          <Navbarright {...NavbarRightItemsProps} className="navbarright"/>
        </div>
    );
}

export default withStyles(Topnav, TopnavStyle);
export {Topnav as TopnavNotStyled}
