import React from "react";
import PropTypes from "prop-types";
import withStyles from "../../withstyle";
import { WriteCommentcardStyle } from "./WriteCommentcard.style";
import Image from "../../atoms/Image/image";
import Comment from "../../atoms/TextArea/text-area";
const WriteCommentcard = ({ className, imgsrc, textAreaonChange }) => {
  return (
    <div className={`${className}`}>
      <div className="WriteCommentmain" data-testid="writecomment">
        <Image
          src={imgsrc}
          alt="Profile picture of the User"
          varient="small-radius"
          data-testid="image1"
        />

        <Comment
          type="text"
          className="comment-box"
          onChange={textAreaonChange}
          placeholder="Write a Comment..."
          rows={4}
          col={50}
        />
      </div>
    </div>
  );
};
WriteCommentcard.propTypes = {
  imgsrc: PropTypes.string,
  textAreaonChange: PropTypes.func,
};
WriteCommentcard.defaultProps = {
  imgsrc:
    "data:image/png;base64,",
};

export default withStyles(WriteCommentcard, WriteCommentcardStyle);
export { WriteCommentcard as WriteCommentcardNotStyled };
